import React from 'react'
import { graphql } from 'gatsby'
import ScrollAnimation from 'react-animate-on-scroll'
import Layout from '../components/layout'
import ProjectCard from '../components/common/ProjectCard'
import styled from 'styled-components'

const ProjectsContainer = styled.div`
  margin-top: 5rem;
  padding: 0 2rem;
`

const ProjectsList = styled.ul`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  li {
    flex-basis: 50%;

    &:nth-child(even) {
      padding-top: 6rem;
    }
  }
`

const Projects = ({
  data: {
    allMarkdownRemark: { edges },
  },
}) => {
  return (
    <Layout themeColor="white">
      <ProjectsContainer>
        <h1>Projects</h1>
        <ProjectsList>
          {edges &&
            [...edges, ...edges, ...edges, ...edges].map(edge => {
              console.log({ edge })
              const { title, featuredImage, slug } = edge.node.frontmatter
              return (
                <li>
                  <ScrollAnimation animateIn="fadeInUp">
                    <ProjectCard
                      title={title}
                      slug={slug}
                      featured_image={featuredImage.childImageSharp.fluid}
                    />
                  </ScrollAnimation>
                </li>
              )
            })}
        </ProjectsList>
      </ProjectsContainer>
    </Layout>
  )
}

export default Projects

export const projectsQuery = graphql`
  query {
    allMarkdownRemark {
      edges {
        node {
          frontmatter {
            title
            slug
            featuredImage {
              childImageSharp {
                fluid(maxWidth: 300) {
                  ...GatsbyImageSharpFluid_withWebp
                }
              }
            }
          }
        }
      }
    }
  }
`
