import React from 'react'
import Img from 'gatsby-image'
import AniLink from 'gatsby-plugin-transition-link/AniLink'
import styled from 'styled-components'
import ScrollAnimation from 'react-animate-on-scroll'

const StyledLink = styled(AniLink)`
  text-decoration: none;
  color: black;
`

const StyledContainer = styled.div`
  display: flex;
  flex-direction: column;

  &:not(:nth-child(even)) {
    margin-right: 2rem;
  }

  &:hover {
    figure {
      transform: scale(1.2);
    }
    .scaler {
      transform: scale(0.98);
    }

    .project-card-cta:after {
      width: 100%;
      animation: link-hover 0.8s cubic-bezier(0.58, 0.3, 0.005, 1) 0s 1;
    }
  }

  figure {
    transition: 0.3s ease-in-out;
    margin: 0;
  }

  & > * {
    flex-basis: 50%;
  }

  .scaler {
    transition: transform 0.3s ease-in-out;
    overflow: hidden;
  }

  .project-card-top {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .project-card-bottom {
    .project-card-title {
      margin: .5rem 0;
      line-height: 1.2;
    }
    .project-card-cta {
      align-self: flex-start;
      position: relative;
      background: transparent;
      overflow: hidden;
      border: 0;
      font-size: 0.8em;
      text-align: center;
      cursor: pointer;
      -webkit-appearance: none;
      width: fit-content;

      &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        height: 1px;
        width: 100%;
        background-color: #099fc0;
        -webkit-transition: width 0.3s ease;
        transition: width 0.3s ease;
      }
  }

`

const ProjectCard = ({ title, featured_image, slug }) => {
  const url = `/projects/${slug}`

  return (
    <StyledLink
      to={url}
      cover
      bg={'black'}
      direction="right"
      className="project-card"
    >
      <StyledContainer>
        <div className="project-card-top">
          {featured_image ? (
            <div className="scaler">
              <ScrollAnimation animateIn="scale-down" delay={150}>
                <figure className="project-feature-image">
                  <Img fluid={featured_image} alt={title} />
                </figure>
              </ScrollAnimation>
            </div>
          ) : null}
        </div>
        <div className="project-card-bottom">
          <h2 className="project-card-title">{title}</h2>
          <div className="project-card-cta">
            <span>See project</span>
          </div>
        </div>
      </StyledContainer>
    </StyledLink>
  )
}

export default ProjectCard
